@import 'partials/_global-class';
@import 'partials/_normalize';
@import 'partials/_print';

$color:#00fbE0;
* {
  box-sizing: border-box;
}

body {
  font-family: 'Nunito', sans-serif;
}

.container {
  max-width: 1350px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.main-hero {
  $c: rgba(#000, 0.3);
  $c2: rgba(#000, 0.6);
  height: 100vh;
  background: #333333;
  background-image: linear-gradient(90deg, $c2, $c, $c2),
  linear-gradient($c, $c),
  url("../images/main-banner3.jpg");
  background-size: cover;
}

.main-hero .container {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.page-header {
  width: 100%;
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.page-logo {

}

.page-logo img {

}

.page-nav {

}


.page-nav-list {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0;


}

.page-nav-list li {
  list-style: none;
}

.page-nav-list a {
  text-transform: uppercase;
  color: rgba(white,0.7);
  text-decoration: none;
  padding:10px 20px;
  position: relative;
}
.page-nav-list a:after{
  content: '';
  position: absolute;
  bottom: 0;
  left:50%;
  right: 50%;
  height: 2px;
  background: $color;
  transition: 0.5s all;
}
.page-nav-list a:hover{
  color: $color;
}
.page-nav-list a:hover:after{
  left: 10px;
  right: 10px;
}
.main-banner {
flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main-title{
  max-width: 80%;
  width: 100%;
}

.main-banner-letter-type-1 {
  fill-opacity: 0;
  fill: $color;
  stroke:$color;
 
}
.main-banner-letter-type-2 {
  fill-opacity: 0;
  fill: #fff;
  stroke: #fff;
}

$offset: 400 400 620 510 400 610 460 880 600 570 320 580;

@for $i from 1 through 12 {
  .main-banner-letter-#{$i}  {
    stroke-dasharray:nth($offset, $i);
    stroke-dashoffset:nth($offset, $i);
    $delay:(1.5 + 0.3 *$i )*1s;
    $delay2:(3 + 0.3 *$i )*1s;

    animation:
            animMainBannerStroke 0.5s $delay 1 linear forwards,
            animMainBannerTitleFill 1s $delay2 1 linear forwards; }

}
